// /* eslint-disable */
document.addEventListener('DOMContentLoaded', () => {
  const heroAnimation = document.querySelectorAll('.hero-animation');
  heroAnimation.forEach((hero) => {
    const layerA = hero.querySelector('.hero-animation__anima-layer-1');
    const layerB = hero.querySelector('.hero-animation__anima-layer-2');

    if (!layerA || !layerB) return;

    let posA = 0; // Posici칩n inicial para layerA
    let posB = 0; // Posici칩n inicial para layerB

    setInterval(() => {
      // Incrementa las posiciones para crear un efecto de movimiento
      posA -= 0.2; // Velocidad lenta para layerA
      posB -= 0.5; // Velocidad m치s r치pida para layerB

      // Aplica las posiciones a los elementos
      layerA.style.backgroundPosition = `${posA}px 30px`;
      layerB.style.backgroundPosition = `${posB}px 0px`;
    }, 12); // Ajusta el intervalo de tiempo para suavizar el movimiento
  });
});
